import React from "react"
import { Link, useStaticQuery, graphql } from "gatsby"

import Layout from "../components/layout"
import Image from "../components/image"
import SEO from "../components/seo"
import Nav from "../components/nav"
import IndexHero from "../components/indexHero"
import OurServicesMid from "../components/ourServicesMid"
import { ParallaxProvider, ParallaxBanner } from 'react-scroll-parallax';
import AboutUs from "../components/aboutUs";
const IndexPage = () => {
  const { gcms } = useStaticQuery(graphql`
  {
    gcms {
      videos {
        indexVideo {
          url
        }
      }
    }
}`)

  return(<Layout>
    <SEO title="Hem" />

    <ParallaxProvider>
      <div style={{ width: "100%" }}>
        <IndexHero />
        <section style={{display:"flex", marginTop:"5vw", alignItems:"center", flexDirection:"column"}}>
        <h1 style={{ textAlign: "center", marginBottom: "2rem" }}>
                Om oss
            </h1>
          <video  width="50%" controls>
            <source src={gcms.videos[0].indexVideo.url}/>
          </video>
        </section>
        <AboutUs hidden={true}/>
      </div>
    </ParallaxProvider>

  </Layout>)

}
export default IndexPage
