import React from "react"
import { useStaticQuery, graphql } from "gatsby"
import ParallaxBanner  from './parallaxBanner';


const IndexHero = () => {
    
    const {gcms} = useStaticQuery(graphql`
    {
        gcms {
        images {
            bannerImage {
              url
            }
          }
    }
}
  `)

    return (
        <ParallaxBanner url={gcms.images[0].bannerImage.url} heading={"Välkommen till Victoria Golv"} subheading={"Golvarbete av högsta kvalité"}/>
        
    )
}

export default IndexHero;